async function parseCardsInMessage(message: string) {
  console.log("üîç Parsing message for cards:", message);
  console.log("üîç Message length:", message.length);
  console.log("üîç Message includes [CARD:", message.includes("[CARD:"));

  // –ò—â–µ–º –º–∞—Ä–∫–µ—Ä—ã —Å —É—á–µ—Ç–æ–º –≤–æ–∑–º–æ–∂–Ω—ã—Ö –ø—Ä–æ–±–µ–ª–æ–≤ –∏ –ø–µ—Ä–µ–Ω–æ—Å–æ–≤ —Å—Ç—Ä–æ–∫
  const cardRegex = /\[CARD:(SPECIALIST|SERVICE):(\d+)\]/gi;
  const cards: any[] = [];
  let match;

  while ((match = cardRegex.exec(message)) !== null) {
    const [fullMatch, type, id] = match;
    const cardId = parseInt(id);
    console.log(`üìå Found card marker: ${fullMatch}, type: ${type}, id: ${cardId}`);

    try {
      if (type === "SPECIALIST") {
        const specialist = await prisma.specialist.findUnique({
          where: { id: cardId },
          select: {
            id: true,
            name: true,
            qualification: true,
            experience: true,
            specialization: true,
            image_url: true,
            education: true,
            category: {
              select: {
                slug: true,
              },
            },
          },
        });

        if (specialist) {
          console.log(`‚úÖ Found specialist:`, specialist);
          cards.push({
            type: "specialist",
            data: {
              ...specialist,
              categorySlug: specialist.category?.slug || "specialists",
            },
            placeholder: fullMatch,
          });
        } else {
          console.log(`‚ùå Specialist ${cardId} not found in DB`);
        }
      } else if (type === "SERVICE") {
        const service = await prisma.service.findUnique({
          where: { id: cardId },
          select: {
            id: true,
            title: true,
            description: true,
            category: {
              select: {
                name: true,
                slug: true,
              },
            },
            price: true,
          },
        });

        if (service) {
          console.log(`‚úÖ Found service:`, service);
          // –ù–æ—Ä–º–∞–ª–∏–∑—É–µ–º –¥–∞–Ω–Ω—ã–µ: category –∏–∑ –æ–±—ä–µ–∫—Ç–∞ –≤ —Å—Ç—Ä–æ–∫—É + –¥–æ–±–∞–≤–ª—è–µ–º slug
          const normalizedService = {
            ...service,
            category: service.category?.name || "–ù–µ —É–∫–∞–∑–∞–Ω–æ",
            categorySlug: service.category?.slug || "services",
          };
          cards.push({
            type: "service",
            data: normalizedService,
            placeholder: fullMatch,
          });
        } else {
          console.log(`‚ùå Service ${cardId} not found in DB`);
        }
      }
    } catch (error) {
      console.error(`‚ùå Error loading card ${type}:${id}:`, error);
    }
  }

  console.log(`üì¶ Total cards found: ${cards.length}`, cards);
  return { message, cards };
}

async function getClinicContext() {
  try {
    console.log("getClinicContext: Fetching contacts...");
    const contacts = await prisma.contacts.findFirst();
    console.log("getClinicContext: Contacts fetched:", contacts ? "‚úì" : "‚úó");

    console.log("getClinicContext: Fetching services...");
    const services = await prisma.service.findMany({
      select: {
        id: true,
        title: true,
        description: true,
        category: {
          select: {
            name: true,
          },
        },
      },
    });
    console.log("getClinicContext: Services fetched:", services.length);

    console.log("getClinicContext: Fetching specialists...");
    const specialists = await prisma.specialist.findMany({
      select: {
        id: true,
        name: true,
        qualification: true,
        experience: true,
        specialization: true,
      },
    });
    console.log("getClinicContext: Specialists fetched:", specialists.length);

    // –ù–æ—Ä–º–∞–ª–∏–∑—É–µ–º –¥–∞–Ω–Ω—ã–µ: –ø—Ä–µ–æ–±—Ä–∞–∑—É–µ–º –≤–ª–æ–∂–µ–Ω–Ω—ã–µ –æ–±—ä–µ–∫—Ç—ã –≤ —Å—Ç—Ä–æ–∫–∏
    const normalizedServices = services.map((s) => ({
      ...s,
      category: s.category?.name || "–ù–µ —É–∫–∞–∑–∞–Ω–æ",
    }));

    return {
      contacts,
      services: normalizedServices,
      specialists,
    };
  } catch (error) {
    console.error("Error fetching clinic context:", error);
    if (error instanceof Error) {
      console.error("Error details:", error.message);
      console.error("Error stack:", error.stack);
    }
    return null;
  }
}

